import { yupResolver } from '@hookform/resolvers/yup';
import emailjs, { init } from 'emailjs-com';
import {
  Button,
  Container,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormLabel,
  InputLabel,
  MenuItem,
  OutlinedInput,
  Radio,
  RadioGroup,
  Select,
  SelectChangeEvent,
  styled,
  TextField,
  ThemeProvider,
  Typography,
} from '@mui/material';
import * as yup from 'yup';
import { SubmitHandler, useForm } from 'react-hook-form';
import style from '../../style/style';
import { SetStateAction, useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
init('OKXxmjNErUNHOPGT8');
const Box = styled('div')`
  margin: 0 auto;
  width: 50%;
`;
const Block = styled('div')`
  display: flex;
  flex-direction: column;

  width: 50%;
`;
const formValidation = yup.object().shape({
  email: yup
    .string()
    .required('** !')
    .max(50, '**      8 !')
    .matches(/^\S+@\S+$/i, '**  !'),
  phone: yup
    .string()
    .required('** !')
    .matches(
      /^[+]*[(]{0,1}[0-9]{1,3}[)]{0,1}[-\s\./0-9]*$/g,
      '**  (+18888888888)!',
    ),
  fname: yup.string().required('** !').max(50, '** !'),
  sname: yup.string().required('** !').max(50, '** !'),
});

const Profile = () => {
  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: 'onTouched',
    resolver: yupResolver(formValidation),
  });
  const params = useParams();
  const [attempt, setAttempt] = useState(true);
  const [job, setJob] = useState({});
  const [univer, setUniver] = useState<string>('');
  const navigate = useNavigate();

  useEffect(() => {
    fetch(`https://api.hh.ru/vacancies/${params.id}`)
      .then((res) => res.json())
      .then((data) => setJob(data));
  }, [params.id]);

  const handleSubmitToJob = (user: any, job: any) => {
    const templateId = 'template_bblkwws';
    const message = `
      ,  ${user.fname} ${user.sname},      !
          :\n
       : ${user.phone};\n
      : ${user.email};\n
    `;
    sendFeedback(templateId, {
      message: message,
      from_name: `${user.fname} ${user.sname}`,
      to_name: job.employer.name,
    });
  };
  const sendFeedback = (templateId: string, variables: Record<string, unknown> | undefined) => {
    emailjs
      .send('service_jq_01', templateId, variables)
      .then((res) => {
        alert(' !');
      })
      .catch((err) =>
        console.error('Oh well, you failed. Here some thoughts on the error that occured:', err),
      );
  };
  const onSubmit = (data: any) => {
    localStorage.setItem('profile', JSON.stringify(data));
    handleSubmitToJob(data, job);
    navigate('/');
  };

  return (
    <ThemeProvider theme={style}>
      <Container style={{ padding: '20px', textAlign: 'center' }}>
        <Box>
          <Typography fontSize={50}> </Typography>
          <form
            style={{
              display: 'flex',
              flexDirection: 'column',
              textAlign: 'center',
              margin: '0 auto',
              padding: '20px',
              borderRadius: '10px',
              boxShadow: '0px 1px 4px rgba(0, 0, 0, 0.25)',
            }}
            onSubmit={handleSubmit(onSubmit)}>
            <Typography color="secondary" fontSize={30}>
               
            </Typography>
            <FormControl sx={{ m: 1 }} variant="outlined">
              <InputLabel htmlFor="email">*E-mail</InputLabel>
              <OutlinedInput {...register('email')} id="email" type="text" label="E-mail" />
            </FormControl>
            <FormControl sx={{ m: 1 }} variant="outlined">
              <InputLabel htmlFor="phone">* </InputLabel>
              <OutlinedInput {...register('phone')} id="phone" type="text" label="phone" />
            </FormControl>
            <FormControl sx={{ m: 1 }} variant="outlined">
              <InputLabel htmlFor="fname">*</InputLabel>
              <OutlinedInput {...register('fname')} id="fname" type="text" label="fname" />
            </FormControl>
            <FormControl sx={{ m: 1 }} variant="outlined">
              <InputLabel htmlFor="sname">*</InputLabel>
              <OutlinedInput {...register('sname')} id="sname" type="text" label="sname" />
            </FormControl>
            <Typography color="secondary" fontSize={30}>
               
            </Typography>
            <Block>
              <FormControl sx={{ m: 1 }} variant="outlined">
                <TextField
                  id="date"
                  label=" "
                  type="date"
                  size="medium"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  {...register('brth')}
                  style={{ marginTop: '25px' }}
                />
              </FormControl>
            </Block>
            <FormControl style={{ display: 'flex', textAlign: 'left', margin: '10px 10px' }}>
              <FormLabel id="demo-radio-buttons-group-label">:</FormLabel>
              <RadioGroup
                row
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="female"
                name="radio-buttons-group">
                <FormControlLabel
                  value="Female"
                  control={<Radio {...register('gender')} />}
                  label=""
                />
                <FormControlLabel
                  value="Male"
                  control={<Radio {...register('gender')} />}
                  label=""
                />
                <FormControlLabel
                  value="Other"
                  control={<Radio {...register('gender')} />}
                  label=""
                />
              </RadioGroup>
            </FormControl>
            <FormControl sx={{ m: 1 }} variant="outlined">
              <InputLabel htmlFor="citizenship"></InputLabel>
              <OutlinedInput
                {...register('citizenship')}
                id="citizenship"
                type="text"
                label="E-mail"
              />
            </FormControl>
            <FormControl style={{ display: 'flex', textAlign: 'left', margin: '10px 10px' }}>
              <FormLabel id="demo-radio-buttons-group-label"> :</FormLabel>
              <RadioGroup
                row
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="no"
                name="radio-buttons-group">
                <FormControlLabel
                  value=""
                  control={<Radio {...register('experience')} />}
                  label=""
                />
                <FormControlLabel
                  value=""
                  control={<Radio {...register('experience')} />}
                  label=""
                />
              </RadioGroup>
            </FormControl>
            <Typography color="secondary" fontSize={30}>
              
            </Typography>
            <Block>
              <FormControl sx={{ m: 1 }} variant="outlined">
                <InputLabel id="univer"></InputLabel>
                <Select
                  style={{}}
                  labelId="univer"
                  id="demo-multiple-name"
                  value={univer}
                  {...register('univer')}
                  onChange={(e) => setUniver(e.target.value)}>
                  <MenuItem value="" key="1">
                    
                  </MenuItem>
                  <MenuItem value="" key="2">
                    
                  </MenuItem>
                  <MenuItem value="" key="3">
                    
                  </MenuItem>
                  <MenuItem value="" key="4">
                    
                  </MenuItem>
                </Select>
              </FormControl>
              <FormControl sx={{ m: 1 }} variant="outlined">
                <InputLabel htmlFor="degree"></InputLabel>
                <OutlinedInput {...register('degree')} id="degree" type="text" label="E-mail" />
              </FormControl>
              <FormControl sx={{ m: 1 }} variant="outlined">
                <InputLabel htmlFor="specialty"></InputLabel>
                <OutlinedInput
                  {...register('specialty')}
                  id="specialty"
                  type="text"
                  label="E-mail"
                />
              </FormControl>
            </Block>
            <Button type="submit" style={{}} size="large" color="secondary" variant="outlined">
              
            </Button>
          </form>
        </Box>
      </Container>
    </ThemeProvider>
  );
};
export default Profile;
